export default {
       onShareAppMessage: function(ops) {
    if (ops.from === "button") {
      // 来自页面内转发按钮
      console.log(ops.target);
    }
    return {
    title: "大可爱",//这里是定义转发的标题
    path: `pages/index/index`,//这里是定义转发的地址
      success: function(res) {
        // 转发成功
        console.log("转发成功:" + JSON.stringify(res));
        var shareTickets = res.shareTickets;
      },
      fail: function(res) {
        // 转发失败
        console.log("转发失败:" + JSON.stringify(res));
      }
    };
  },
  data () {
    return {
        songPanel:{},
        ning:''
    }
  },
  components: {
  },
  onLoad(){
      this.init();
  },
  methods: {
      init(){
		  uni.request({
			url:'https://v1.alapi.cn/api/comment/',
			success: (res) => {
				this.songPanel = res.data.data;
			}
		  }); 
      }
  }
}
